////////// FONTS//

$type1:     'Ubuntu', sans-serif;

$font-weight-light: 300;
$font-weight-medium: 500;
$font-weight-bold: 700;

$default-font-size: .875rem; // 14px as base font size

$text-muted: #9c9fa6;
$body-color: #fff;

////////// FONT VARIABLES //////////

///////// CARD ////////
$card-bg: linear-gradient(to bottom, #222328, #1a191d);
$card-description-color: #76838f;
$card-title-color: $body-color;
$card-bg-varient: #222328;
///////// CARD ////////


///////// NAVBAR ////////

$navbar-brand-wrapper-mini-width: 70px;
$menu-break-point: 1020px;
$horizontal-navbar-height: 135px;
$horizontal-top-bg : $content-bg;
$horizontal-top-navbar-height: 70px;
$horizontal-menu-padding: 0 1rem;
$horizontal-menu-height:  57px;
$horizontal-top-menu-item-color: #9c9fa6;
$horizontal-top-font-size: 1rem;
$horizontal-bottom-bg: $primary;
$horizontal-bottom-menu-item-color: $white;
$horizontal-bottom-menu-dropdown-bg: $content-bg;
$horizontal-bottom-menu-dropdown-item-color: $body-color;
$horizontal-bottom-menu-item-icon-color: $white;
$horizontal-bottom-menu-item-active-color: lighten($primary, 18%);
$horizontal-menu-submenu-color: #202339;
$horizontal-bottom-menu-item-font-size: .875rem;
$horizontal-bottom-menu-item-hover-bg: transparent;
$horizontal-bottom-menu-item-hover-color: lighten($primary, 18%);
$horizontal-menu-item-padding: .75rem 0;
$horizontal-menu-icon-font-size: 1.25rem;
$horizontal-menu-submenu-item-padding: .85rem 0;
$horizontal-menu-box-shadow: 0 19px 34px -15px $content-bg;

///////// NAVBAR ////////



///////// FOOTER ////////
$footer-height: 75px;
///////// FOOTER ////////

///////// BUTTONS ////////

$button-fixed-width:        150px;
$btn-padding-y:       .875rem;
